html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans", sans-serif;
  background-color: #E4E4E4;
  color: #444444;
}

.container {
  width: calc(100% - 24px);
  max-width: 990px;
  margin: 0 12px;
  padding: 0 12px;
  line-height: 2rem;
}
@media only screen and (min-width: 768px) {
  .container {
    width: 100%;
    margin: 0 auto;
  }
}

.container .row {
  display: flex;
  flex-wrap: wrap;
}
.container .row [class*=col-] {
  min-height: 1px;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .container .row .col-1 {
    width: 8.3333333333%;
    padding: 0 12px;
  }
  .container .row .col-2 {
    width: 16.6666666667%;
    padding: 0 12px;
  }
  .container .row .col-3 {
    width: 25%;
    padding: 0 12px;
  }
  .container .row .col-4 {
    width: 33.3333333333%;
    padding: 0 12px;
  }
  .container .row .col-5 {
    width: 41.6666666667%;
    padding: 0 12px;
  }
  .container .row .col-6 {
    width: 50%;
    padding: 0 12px;
  }
  .container .row .col-7 {
    width: 58.3333333333%;
    padding: 0 12px;
  }
  .container .row .col-8 {
    width: 66.6666666667%;
    padding: 0 12px;
  }
  .container .row .col-9 {
    width: 75%;
    padding: 0 12px;
  }
  .container .row .col-10 {
    width: 83.3333333333%;
    padding: 0 12px;
  }
  .container .row .col-11 {
    width: 91.6666666667%;
    padding: 0 12px;
  }
  .container .row .col-12 {
    width: 100%;
    padding: 0 12px;
  }
}

button.button {
  appearance: none;
  border: none;
  font-family: "Open Sans", sans-serif;
  font-weight: 800;
  padding: 24px 48px;
  border-radius: 12px;
  font-size: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  button.button {
    flex-direction: row-reverse;
  }
}
button.button i {
  padding-right: 12px;
}
@media only screen and (min-width: 768px) {
  button.button i {
    padding-right: initial;
    padding-left: 12px;
  }
}
button.button--active {
  margin: 2rem 0;
  background-color: #1691C3;
  color: white;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  button.button--active {
    width: 50%;
  }
}
button.button_tumbnails {
  min-width: 313px;
}

header.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 36px auto;
}
@media only screen and (min-width: 768px) {
  header.container {
    justify-content: space-between;
  }
}
header.container .header__logo {
  font-family: "Oleo Script", cursive;
  background: #FC4887;
  color: white;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 36px;
}
header.container input.header__nav__button {
  appearance: none;
  position: fixed;
  left: 12px;
  top: 12px;
  font-size: 24px;
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(120, 120, 120, 0.5);
  border-radius: 12px;
  color: white;
}
@media only screen and (min-width: 768px) {
  header.container input.header__nav__button {
    display: none;
  }
}
header.container input.header__nav__button:checked {
  left: 292px;
}
header.container input.header__nav__button:checked ~ nav.header__nav {
  left: 0;
}
header.container nav.header__nav {
  width: 280px;
  background-color: white;
  position: fixed;
  left: -280px;
  top: 0;
  bottom: 0;
  height: 100%;
  overflow-y: auto;
  border-right: 5px solid #b8b8b8;
}
@media only screen and (min-width: 768px) {
  header.container nav.header__nav {
    position: static;
    background-color: initial;
    overflow: initial;
    border: none;
    flex-grow: 1;
  }
}
@media only screen and (min-width: 768px) {
  header.container nav.header__nav ul.menu {
    text-align: right;
  }
}
header.container nav.header__nav ul.menu li {
  font-weight: bold;
  padding: 24px;
  border-bottom: 1px solid #b8b8b8;
}
@media only screen and (min-width: 768px) {
  header.container nav.header__nav ul.menu li {
    border: none;
    display: inline-block;
    padding: 12px;
  }
}
header.container nav.header__nav ul.menu li:last-child {
  border: none;
}
header.container nav.header__nav ul.menu ul.submenu {
  border-top: 1px solid #b8b8b8;
  margin-top: 24px;
  margin-bottom: -24px;
}
@media only screen and (min-width: 768px) {
  header.container nav.header__nav ul.menu ul.submenu {
    display: none;
  }
}
header.container nav.header__nav ul.menu ul.submenu li {
  font-weight: normal;
  color: #b8b8b8;
}

section.banner.container {
  background: white;
  border-radius: 12px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  section.banner.container {
    flex-direction: row;
  }
}
section.banner.container .texts {
  margin-bottom: 24px;
}
@media only screen and (min-width: 768px) {
  section.banner.container .texts {
    flex: 2 0 0;
  }
}
section.banner.container .texts span {
  display: block;
  text-align: center;
}
@media only screen and (min-width: 768px) {
  section.banner.container .texts span {
    text-align: left;
  }
}
section.banner.container .texts span.title {
  font-weight: bold;
  font-size: 36px;
  padding: 24px;
}
section.banner.container .texts span.content {
  color: #696969;
  font-size: 24px;
  padding: 12px 24px;
}
section.banner.container .buttons {
  width: 100%;
}
@media only screen and (min-width: 768px) {
  section.banner.container .buttons {
    width: auto;
    flex: 1 0 0;
  }
}
section.banner.container .buttons .button {
  width: 100%;
  margin-bottom: 12px;
}

.articles {
  padding: 0 -12px;
  margin: 0 -12px;
}
.articles:last-of-type {
  margin-bottom: 50px;
}
.articles .article {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  margin-top: 2em;
}
.articles .article img {
  width: 100%;
  margin-bottom: 3em;
}
.articles .article .article__title {
  padding: 0 0.7em;
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: 0.5em;
}
.articles .article .article__text {
  color: #696969;
  font-size: 0.95em;
  padding: 0 1.15em;
  margin-bottom: 2em;
}

.background-wraper {
  background-color: white;
  box-shadow: 0 3 0 0 rgba(0, 0, 0, 0.05);
}
.background-wraper .tumbnails {
  display: flex;
  flex-direction: column-reverse;
}
@media only screen and (min-width: 768px) {
  .background-wraper .tumbnails {
    flex-direction: row;
  }
}
.background-wraper .tumbnails .tumbnails-content {
  border-bottom: 1px solid #e9e9e9;
  padding-bottom: 0.8rem;
  margin-bottom: 3rem;
}
@media only screen and (min-width: 768px) {
  .background-wraper .tumbnails .tumbnails-content {
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 768px) {
  .background-wraper .tumbnails .tumbnails-content {
    width: 66.66667%;
    padding-left: 1rem;
    border: none;
  }
}
.background-wraper .tumbnails .tumbnails-content .tumbnails-content_title {
  font-size: 1.25em;
  font-weight: bold;
  margin: 3rem 0 1.5rem 0;
}
.background-wraper .tumbnails .tumbnails-content .tumbnails-content_paragraph {
  color: #696969;
  margin-bottom: 2rem;
}
@media only screen and (min-width: 768px) {
  .background-wraper .tumbnails .tumbnails-sidebar {
    width: 33.33333%;
    padding-right: 1rem;
  }
}
.background-wraper .tumbnails .tumbnails-sidebar .tumbnails-sidebar_title {
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: 2rem;
}
@media only screen and (min-width: 768px) {
  .background-wraper .tumbnails .tumbnails-sidebar .tumbnails-sidebar_title {
    font-size: 1.25em;
    margin: 3rem 0 1.5rem 0.4rem;
  }
}
.background-wraper .tumbnails .tumbnails-sidebar .tumbnails-sidebar_images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.background-wraper .tumbnails .tumbnails-sidebar .tumbnails-sidebar_images img {
  overflow: hidden;
  padding: 0.3rem;
  border-radius: 15px;
  width: 50%;
}

.row {
  color: #444444;
  display: flex;
  flex-wrap: wrap;
  margin: 3rem;
}
@media only screen and (min-width: 768px) {
  .row {
    margin: 0;
  }
}
.row [class*=col-] {
  min-height: 1px;
  width: 8.3333333333%;
}
.row .col-1 {
  width: 8.3333333333%;
}
.row .col-2 {
  width: 16.6666666667%;
}
.row .col-3 {
  width: 25%;
}
.row .col-4 {
  width: 33.3333333333%;
}
.row .col-5 {
  width: 41.6666666667%;
}
.row .col-6 {
  width: 50%;
}
.row .col-7 {
  width: 58.3333333333%;
}
.row .col-8 {
  width: 66.6666666667%;
}
.row .col-9 {
  width: 75%;
}
.row .col-10 {
  width: 83.3333333333%;
}
.row .col-11 {
  width: 91.6666666667%;
}
.row .col-12 {
  width: 100%;
}
.row .widget {
  border-bottom: 1px solid #ccc;
  padding-bottom: 2rem;
  margin-bottom: 1rem;
}
@media only screen and (min-width: 768px) {
  .row .widget {
    border: none;
  }
}
.row .widget h3 {
  color: black;
  font-size: 1.25rem;
  font-weight: 800;
  margin: 1rem 0;
}
.row li {
  font-size: 0.9rem;
}
.row .contact ul {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 1rem;
}
.row .contact ul span {
  display: none;
}
.row .contact ul li {
  color: #E4E4E4;
  width: 50px;
  height: 50px;
  background-color: #222222;
  border-radius: 10px;
  text-align: center;
  padding: 10px;
}
@media only screen and (min-width: 768px) {
  .row .contact ul li {
    width: 40px;
    height: 40px;
  }
}
@media only screen and (min-width: 768px) {
  .row .copyright {
    text-align: center;
  }
  .row .copyright li {
    display: inline-block;
    text-align: center;
    padding: 0 1.5rem;
  }
  .row .copyright li:first-of-type {
    border-right: 1px solid #ccc;
  }
}